.topNav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px auto;
  position: absolute;
  left: 50%;
  z-index: 1000000000000;
  transform: translateX(-50%);
}
.logo {
  text-align: center;
  color: #fff;
}
.logo h3 {
  font-weight: 400;
}
.icon {
  display: flex;
  align-items: center;
  gap: 10px;
}
.icon li {
  width: 35px;
  height: 35px;
  line-height: 40px;
  text-align: center;
  border-radius: 50%;
  color: #1eb2a6;
  background-color: rgba(255, 255, 255, 0.3);
  transition: 0.3s;
}
.icon li:hover {
  color: #fff;
  background-color: #1eb2a6;
}
@media (max-width: 530px) {
  .logo h2 {
    font-size: 20px;
  }
  .logo h3 {
    font-size: 15px;
  }
}
@media (max-width: 490px) {
  .icon {
    display: none;
  }
}
