.home {
  position: relative;
}
.home_img {
  position: relative;
  top: 0;
  left: 0;
}
.home_info {
  position: absolute;
  top: 300px;
  left: 150px;
  width: 40%;
  color: #fff;
}
.home_info span {
  font-weight: 500;
  font-size: 25px;
}
.home_info h2 {
  font-size: 40px;
  margin: 10px 0;
}
.home_info p {
  line-height: 30px;
  opacity: 0.8;
}
.allBtn {
  display: flex;
  gap: 20px;
  margin-top: 15px;
}
.allBtn button {
  padding: 15px 20px;
  background-color: #1eb2a6;
  display: flex;
  align-items: center;
  gap: 5px;
  border-radius: 2px;
}
.allBtn button span {
  font-size: 12px;
  font-weight: 600;
}
.allBtn .btnOne {
  color: #fff;
}
.allBtn .btnTwo {
  background-color: #fff;
  color: #1eb2a6;
}
@media (max-width: 993px) {
  .home_info h2 {
    font-size: 30px;
  }
  .home_info {
    top: 250px;
    left: 120px;
  }
}
@media (max-width: 870px) {
  .home_info h2 {
    font-size: 30px;
  }
  .home_info {
    width: 60%;
  }
}
@media (max-width: 757px) {
  .home_info {
    top: 200px;
    left: 80px;
  }
  .home_img img {
    width: 100%;
    height: 500px;
  }
}
@media (max-width: 757px) {
  .home_info {
    top: 180px;
    left: 40px;
    width: 80%;
  }
}
@media (max-width: 450px) {
  .home_info {
    width: 90%;
  }
  .home_info h2 {
    font-size: 25px;
  }
  .home_info span {
    font-size: 15px;
  }
}
@media (max-width: 450px) {
  .allBtn {
    flex-direction: column;
    gap: 0;
  }
  .allBtn button {
    width: 180px;
  }
}
@media (max-width: 360px) {
  .home_info {
    left: 10px;
    width: 95%;
  }
  .home_img img {
    height: 530px;
  }
}
