.allBoxOnline {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, auto));
  margin-top: 20px;
  gap: 20px;
}
.allBoxOnline .boxOnline {
  background-color: #fff;
  padding: 20px;
}
.allBoxOnline .boxOnline:hover {
  background-color: #1eb2a6;
}
.allBoxOnline .allImg {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto;
}
.allBoxOnline .allImg img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.boxOnline:hover .allImg .second {
  opacity: 0;
}
.boxOnline h2 {
  font-size: 15px;
  margin: 20px 0;
}
.boxOnline {
  transition: 0.3s;
}
.boxOnline:hover h2 {
  color: #fff;
}
.boxOnline:hover .btnOnline {
  color: #1eb2a6;
}
.btnOnline {
  padding: 10px 15px;
  background-color: #f9f9f9;
  border-radius: 2px;
}
@media (max-width: 400px) {
  .courses .heading h2 {
    font-size: 28px;
  }
}
