.allTest {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(340px, auto));
  gap: 20px;
  margin-top: 20px;
}
.allTest .box {
  background-color: #fff;
  transition: 0.3s;
  padding: 20px;
}
.allTest .box:hover {
  background-color: #1eb2a6;
  color: #fff;
}
.allTest .box .topTest {
  display: flex;
  align-items: center;
}
.allTest .box .topTest .img {
  width: 25%;
  border-radius: 50%;
  position: relative;
}
.allTest .box .topTest .img img {
  margin: auto;
  border-radius: 50%;
  width: 100px;
  height: 100px;
}
.allTest .box .topTest .img span {
  position: absolute;
  top: 70%;
  right: 10px;
  color: #fff;
  width: 30px;
  height: 30px;
  background-color: #1eb2a6;
  text-align: center;
  border-radius: 50%;
  line-height: 35px;
  transition: 0.3s;
}
.allTest .box:hover .topTest .img span {
  color: #1eb2a6;
  background-color: #fff;
}

.allTest .box .topTest .testInfo {
  width: 70%;
}
.allTest .box p {
  margin-top: 20px;
}
@media (max-width: 1500px) {
  .allTest .box .topTest h2 {
    font-size: 1.2rem;
  }
  .allTest .box .topTest h3 {
    font-size: 0.9rem;
  }
}
@media (max-width: 500px) {
  .allTest .box .topTest .img span {
    right: 0px;
  }
}
@media (max-width: 400px) {
  .allTest {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, auto));
    gap: 20px;
    margin-top: 20px;
  }
}
@media (max-width: 370px) {
  .allTest .box .topTest h2 {
    font-size: 0.8rem;
  }
  .allTest .box .topTest h3 {
    font-size: 0.7rem;
  }
  .allTest .box .topTest .img span {
    right: -20px;
  }
}
