.containerNavBottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  background-color: rgba(255, 255, 255, 0.2);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 80px;
  padding: 0px;
  z-index: 1000000000000;
}
.navBar ul {
  display: flex;
  align-items: center;
}
.navBar ul li {
  color: #fff;
  margin: 0 15px;
}
.navBar ul li a {
  transition: 0.3s;
  color: #fff;
}
.navBar ul li:hover a {
  color: #1eb2a6;
}
.containerNavBottom .btn button {
  padding: 28px 50px;
  background-color: #1eb2a6;
  color: #fff;
  clip-path: polygon(16% 0, 100% 0%, 100% 100%, 0% 100%);
}
.logoNav {
  display: none;
}
.logoNav h2 {
  color: #fff;
  font-size: 15px;
}
.menu {
  color: #fff;
  font-size: 30px;
  display: none;
}
@media (max-width: 850px) {
  .containerNavBottom {
    padding: 17px;
  }
  .containerNavBottom .btn {
    display: none;
  }
  .logoNav {
    display: block;
  }
  .containerNavBottom {
    width: 100%;
  }
  .menu {
    display: block;
  }
  .navBar {
    height: 300px;
    position: absolute;
    background-color: #1eb2a6;
    width: 100%;
    padding: 20px 30px;
    transition: 0.4s;
    top: 70px;
    left: 0;
    overflow: hidden;
  }
  .navBar ul {
    flex-direction: column;
    align-items: flex-start;
    transition: 0.4s;
    gap: 20px;
  }
  .navBottomActive {
    height: 0px;
    padding: 0px 30px;
  }
}
