.allPricing {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, auto));
  gap: 20px;
  margin-top: 50px;
}
.allPricing .box {
  background-color: #fff;
  text-align: center;
  padding: 50px 30px;
}
.allPricing .box h3 {
  margin: 10px 0;
}
.allPricing .box h3 span {
  color: #1eb2a6;
  font-size: 40px;
}
.allPricing .box p {
  opacity: 0.6;
  line-height: 25px;
}
.allPricing .box button {
  padding: 0px 50px;
  margin-top: 10px;
}
