.courses {
  text-align: center;
}
.courses .heading span {
  color: #1eb2a6;
  font-weight: 600;
  font-size: 20px;
}
.courses .heading h2 {
  font-size: 40px;
}

.allBoxCourses {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, auto));
  gap: 30px;
  margin-top: 40px;
}
.allBoxCourses .box {
  background-color: #fff;
  padding: 15px 60px;
  border-radius: 2px;
}
.allBoxCourses .box .head {
  font-size: 20px;
}
.allBoxCourses .box .allSpan {
  margin: 10px 0;
}
.allBoxCourses .box .allSpan span {
  font-size: 20px;
  color: #1eb2a6;
}
.img {
  display: flex;
  align-items: center;
}
.img .imgss {
  width: 50%;
}
.img .imgss img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
}
.img h2 {
  width: 50%;
  font-size: 16px;
  color: #999;
}
.info h3 {
  color: #1eb2a6;
  margin: 10px 0;
}
.info h1 {
  color: #1eb2a6;
  font-size: 15px;
}
button {
  height: 50px;
  border: 2px solid #1eb2a6;
  margin: 10px 0;
  color: #1eb2a6;
  transition: 0.3s;
  padding: 0 20px;
}
button:hover {
  background-color: #1eb2a6;
  color: #fff;
}

@media (max-width: 450px) {
  .allBoxCourses .box {
    padding: 15px 40px;
  }
}
