.learn_container {
  display: flex;
  align-items: center;
  gap: 50px;
  margin-bottom: 100px;
}
.learn_img {
  width: 60%;
}
.learn_info {
  width: 40%;
}
.learn_info span {
  color: #1eb2a6;
  font-weight: 500;
}
.learn_info h3 {
  margin: 10px 0;
  font-size: 45px;
}
.learn_info .box {
  background-color: #fff;
  margin: 10px 0;
  padding: 20px;
  display: flex;
  gap: 10px;
  transition: 0.3s;
}

.learn_info .box:hover {
  background-color: #1eb2a6;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}
.learn_info .box:hover h2,
.learn_info .box:hover p {
  color: #fff;
}
.learn_info .box .icon {
  font-size: 50px;
}
.learn_info .box p {
  opacity: 0.7;
}
.bottom {
  position: relative;
  margin-bottom: 200px;
}
.bottom .info {
  position: absolute;
  top: 50%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, auto));
  width: 100%;
  gap: 20px;
  text-align: center;
}
.bottom .box {
  display: flex;
  gap: 20px;
  text-align: center;
  margin: auto;
}
.bottom .box .icon {
  font-size: 40px;
}
.bottom .box h2 {
  font-size: 30px;
}

@media (max-width: 1444px) {
  .learn_info h3 {
    font-size: 35px;
  }
}
@media (max-width: 1141px) {
  .learn_container {
    margin-top: 30px;
  }
  .learn_info h3 {
    font-size: 30px;
  }
}
@media (max-width: 1141px) {
  .learn_container {
    flex-direction: column;
  }
  .learn_img {
    width: 70%;
  }
  .learn_info {
    width: 100%;
  }
}
@media (max-width: 760px) {
  .bottom .img img {
    height: 300px;
  }
}
@media (max-width: 630px) {
  .bottom .img img {
    height: 400px;
  }
  .bottom .info {
    padding: 0 50px;
  }
}
@media (max-width: 650px) {
  .bottom {
    margin-bottom: 450px;
  }
}
