.contact {
  margin-top: 30px;
}
.contact .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  background-color: #1eb2a6;
  margin: auto;
  padding: 20px 10%;
}
.contact .top .info {
  width: 50%;
}
.contact .top .info h2 {
  color: #fff;
  font-size: 40px;
}
.contact .top .info h4 {
  color: #fff;
  margin-top: 10px;
  opacity: 0.9;
}
.contact .top form {
  width: 50%;
}
.contact .top form .input {
  position: relative;
}
.contact .top form .input input {
  width: 100%;
  padding: 15px 10px;
  border: none;
  outline: none;
  border-radius: 3px;
}
.contact .top form .input span {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #1eb2a6;
}
.bottom {
  display: grid;
  grid-template-columns: repeat(5, auto);
  gap: 20px;
  margin-top: 40px;
}
.bottom .child h2 {
  margin-bottom: 20px;
}
.bottom .one span {
  font-size: 30px;
}
.bottom .one h2 {
  color: #1eb2a6;
  font-size: 15px;
}
.bottom .one p {
  line-height: 25px;
}
.bottom .one ul {
  display: flex;
  gap: 20px;
}
.bottom .one ul li {
  width: 40px;
  height: 40px;
  background-color: #1eb2a6;
  color: #fff;
  text-align: center;
  border-radius: 50%;
  line-height: 45px;
  cursor: pointer;
  transition: 0.3s;
}
.bottom .one ul li:hover {
  background-color: #fff;
  color: #1eb2a6;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}
.bottom .two li,
.bottom .three li {
  margin: 10px 0;
  color: #1eb2a6;
}
.bottom .two li span,
.bottom .three li span {
  color: black;
  margin-left: 10px;
}
/*  */

.bottom .four li .img {
  width: 20%;
}
.bottom .four li .infoChild {
  width: 80%;
}

.bottom .four span {
  color: #1eb2a6;
  font-size: 10px;
}
.bottom .four h3 {
  font-size: 13px;
}
.bottom .four ul li {
  display: flex;
  margin: 10px 0;
}
.bottom .four ul li .img {
  width: 25%;
}
.bottom .four ul li .img img {
  width: 50px;
  height: 50px;
}
/*  */
.bottom .five li {
  display: flex;
  margin: 15px 0;
  gap: 10px;
  align-items: center;
}
.bottom .five li span {
  color: #1eb2a6;
  font-size: 25px;
}
.bottom .five li h3 {
  font-size: 13px;
}
/* end */
.contact .end {
  text-align: center;
  margin: 30px 0;
}
.contact .end span {
  color: #1eb2a6;
}

@media (max-width: 1490px) {
  .bottom {
    grid-template-columns: repeat(4, auto);
  }
}
@media (max-width: 1100px) {
  .bottom {
    grid-template-columns: repeat(3, auto);
  }
}
@media (max-width: 925px) {
  .bottom {
    grid-template-columns: repeat(2, auto);
  }
}
@media (max-width: 860px) {
  .contact .top {
    flex-direction: column;
  }
  .contact .top .info {
    width: 100%;
  }
}
@media (max-width: 630px) {
  .bottom {
    grid-template-columns: repeat(1, auto);
  }
  .contact .top .info h2 {
    font-size: 30px;
  }
  .contact .top form {
    width: 60%;
  }
}
@media (max-width: 400px) {
  .contact .top .info h2 {
    font-size: 20px;
  }
  .contact .top form {
    width: 70%;
  }
}
